.ToppingsSelector {
  padding: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border-bottom: 1px solid #b8c8d0;
}

.ToppingsSelector-item {
  cursor: pointer;
